import { OrderDataModel } from '@/order/data/models';
import { TaxTypes } from '@/order/types';
import { Price } from '@adobe-commerce/elsie/components';

type translationsTypes = Record<string, string>;

export const Total = ({
  translations,
  shoppingOrdersDisplaySubtotal,
  order,
}: {
  translations: translationsTypes;
  order?: OrderDataModel;
  shoppingOrdersDisplaySubtotal: TaxTypes;
}) => {
  const isFree = order?.grandTotal?.value === 0;

  return (
    <div className="order-cost-summary-content__description order-cost-summary-content__description--total">
      <div className="order-cost-summary-content__description--header">
        <span>{translations.total}</span>
        {!isFree ? (
          <Price
            currency={order?.grandTotal?.currency}
            amount={order?.grandTotal?.value}
            weight="bold"
            size="medium"
          />
        ) : (
          <span className="order-cost-summary-content__description--total-free">
            {translations.totalFree}
          </span>
        )}
      </div>
      {shoppingOrdersDisplaySubtotal.taxExcluded &&
      shoppingOrdersDisplaySubtotal.taxIncluded && !isFree ? (
        <div className="order-cost-summary-content__description--subheader">
          <span>{translations.totalExcludingTaxes}</span>
          <Price
            currency={order?.grandTotalExclTax?.currency}
            amount={order?.grandTotalExclTax?.value}
            weight="normal"
            size="small"
          />
        </div>
      ) : null}
    </div>
  );
};
