import { OrderDataModel } from '@/order/data/models';
import { Accordion, AccordionSection, Price } from '@adobe-commerce/elsie/components';
import { ChevronDown, ChevronUp } from '@adobe-commerce/elsie/icons';
import { useState } from 'preact/hooks';

type translationsTypes = Record<string, string>;

export const Tax = ({
  translations,
  renderTaxAccordion,
  totalAccordionTaxValue,
  order,
}: {
  translations: translationsTypes;
  order: OrderDataModel;
  renderTaxAccordion: boolean;
  totalAccordionTaxValue: number;
}) => {
  const [changeAccordion, setChangeAccordion] = useState(false);
  if (!renderTaxAccordion)
    return (
      <div className="order-cost-summary-content__description order-cost-summary-content__description--tax">
        <div className="order-cost-summary-content__description--header">
          <span>{translations.tax}</span>
          <Price
            currency={order?.totalTax?.currency}
            amount={order?.totalTax.value}
            weight="normal"
            size="small"
          />
        </div>
      </div>
    );

  return (
    <Accordion
      data-testid="tax-accordionTaxes"
      className="order-cost-summary-content__accordion"
      iconOpen={ChevronDown}
      iconClose={ChevronUp}
    >
      <AccordionSection
        onStateChange={setChangeAccordion}
        title={translations.accordionTitle}
        secondaryText={
          <>
            {!changeAccordion ? (
              <Price
                weight="normal"
                amount={totalAccordionTaxValue}
                currency={order?.totalTax?.currency}
              />
            ) : null}
          </>
        }
        renderContentWhenClosed={false}
      >
        {order?.taxes?.map((el, index) => (
          <div
            key={`${el?.amount?.value}${index}`}
            className={'order-cost-summary-content__accordion-row'}
          >
            <p>{el?.title}</p>
            <p>
              <Price
                weight="normal"
                amount={el?.amount?.value}
                currency={el?.amount?.currency}
              />
            </p>
          </div>
        ))}
        <div
          className={
            'order-cost-summary-content__accordion-row order-cost-summary-content__accordion-total'
          }
        >
          <p>{translations.accordionTotalTax}</p>
          <p>
            <Price
              weight="normal"
              amount={totalAccordionTaxValue}
              currency={order.totalTax.currency}
              size="medium"
            />
          </p>
        </div>
      </AccordionSection>
    </Accordion>
  );
};
