import { OrderDataModel } from '@/order/data/models';
import { TaxTypes } from '@/order/types';
import { Price } from '@adobe-commerce/elsie/components';

type translationsTypes = Record<string, string>;

export const PrintedCard = ({
  translations,
  order,
  salesPrintedCard,
}: {
  translations: translationsTypes;
  order?: OrderDataModel;
  salesPrintedCard?: TaxTypes;
}) => {
  const totalGiftOptions = order?.totalGiftOptions;
  const { printedCard, printedCardInclTax } = totalGiftOptions!;

  const isPrintedCardTaxIncluded = salesPrintedCard?.taxIncluded;
  const isPrintedCardTaxExcluded = salesPrintedCard?.taxExcluded;

  if (printedCard.value === 0 || printedCardInclTax.value === 0) {
    return null;
  }

  const taxIncludedView =
    isPrintedCardTaxIncluded && !isPrintedCardTaxExcluded ? (
      <>
        <div className="order-cost-summary-content__description--header">
          <span>{translations.printedCardTitle}</span>
          <span>
            <Price
              className="order-cost-summary-content__description--normal-price"
              weight="normal"
              currency={printedCardInclTax.currency}
              amount={printedCardInclTax.value}
            />
          </span>
        </div>
        <div className="order-cost-summary-content__description--subheader">
          <span>
            <span>{translations.printedCardTitleInclTax}</span>
          </span>
        </div>
      </>
    ) : null;

  const taxExcludedView =
    isPrintedCardTaxExcluded && !isPrintedCardTaxIncluded ? (
      <div className="order-cost-summary-content__description--header">
        <span>{translations.printedCardTitle}</span>
        <span>
          <Price
            weight="normal"
            currency={printedCard.currency}
            amount={printedCard.value}
          />
        </span>
      </div>
    ) : null;

  const taxIncludedAndExcludedView =
    isPrintedCardTaxExcluded && isPrintedCardTaxIncluded ? (
      <>
        <div className="order-cost-summary-content__description--header">
          <span>{translations.printedCardTitle}</span>
          <span>
            <Price
              className="order-cost-summary-content__description--normal-price"
              weight="normal"
              currency={printedCardInclTax.currency}
              amount={printedCardInclTax.value}
            />
          </span>
        </div>
        <div className="order-cost-summary-content__description--subheader">
          <Price
            currency={printedCard.currency}
            amount={printedCard.value}
            size="small"
            weight="bold"
          />{' '}
          <span>{translations.printedCardTitleExclTax}</span>
        </div>
      </>
    ) : null;

  return (
    <div className="order-cost-summary-content__description order-cost-summary-content__description--printed-card">
      {taxIncludedView}
      {taxExcludedView}
      {taxIncludedAndExcludedView}
    </div>
  );
};
