import { MoneyProps, TaxTypes } from '@/order/types';
import { Price } from '@adobe-commerce/elsie/components';

export const GiftWrapping = ({
  salesGiftWrapping,
  giftWrappingPrice,
  giftWrappingPriceInclTax,
  giftWrappingTitle,
  giftWrappingExclTaxText,
  giftWrappingInclTaxText,
}: {
  salesGiftWrapping?: TaxTypes;
  giftWrappingPrice: MoneyProps;
  giftWrappingPriceInclTax: MoneyProps;
  giftWrappingTitle: string;
  giftWrappingExclTaxText: string;
  giftWrappingInclTaxText: string;
}) => {
  const isGiftWrappingTaxIncluded = salesGiftWrapping?.taxIncluded;
  const isGiftWrappingTaxExcluded = salesGiftWrapping?.taxExcluded;
  const isIncludedView =
    isGiftWrappingTaxIncluded && !isGiftWrappingTaxExcluded;
  const isExcludedView =
    isGiftWrappingTaxExcluded && !isGiftWrappingTaxIncluded;
  const isInclExclView = isGiftWrappingTaxExcluded && isGiftWrappingTaxIncluded;

  if (giftWrappingPrice.value === 0 || giftWrappingPriceInclTax.value === 0) {
    return null;
  }

  const taxIncludedView = isIncludedView ? (
    <>
      <div className="order-cost-summary-content__description--header">
        <span>{giftWrappingInclTaxText}</span>
        <Price
          className="order-cost-summary-content__description--normal-price"
          weight="normal"
          currency={giftWrappingPriceInclTax.currency}
          amount={giftWrappingPriceInclTax.value}
        />
      </div>
      <div className="order-cost-summary-content__description--subheader">
        <span>{giftWrappingInclTaxText}</span>
      </div>
    </>
  ) : null;

  const taxExcludedView = isExcludedView ? (
    <div className="order-cost-summary-content__description--header">
      <span>{giftWrappingTitle}</span>
      <span>
        <Price
          weight="normal"
          currency={giftWrappingPrice.currency}
          amount={giftWrappingPrice.value}
        />
      </span>
    </div>
  ) : null;

  const taxIncludedAndExcludedView = isInclExclView ? (
    <>
      <div className="order-cost-summary-content__description--header">
        <span>{giftWrappingTitle}</span>
        <span>
          <Price
            className="order-cost-summary-content__description--normal-price"
            weight="normal"
            currency={giftWrappingPriceInclTax.currency}
            amount={giftWrappingPriceInclTax.value}
          />
        </span>
      </div>
      <div className="order-cost-summary-content__description--subheader">
        <Price
          currency={giftWrappingPrice.currency}
          amount={giftWrappingPrice.value}
          size="small"
          weight="bold"
        />{' '}
        <span>{giftWrappingExclTaxText}</span>
      </div>
    </>
  ) : null;

  return (
    <div className="order-cost-summary-content__description order-cost-summary-content__description--gift-wrapping">
      {taxIncludedView}
      {taxExcludedView}
      {taxIncludedAndExcludedView}
    </div>
  );
};
