import { FunctionComponent } from 'preact';
import { Card, Header } from '@adobe-commerce/elsie/components';
import { classes } from '@adobe-commerce/elsie/lib';
import { OrderCostSummaryContentProps } from '@/order/types';
import { OrderSummarySkeleton } from '@/order/components/OrderLoaders';
import '@/order/components/OrderCostSummaryContent/OrderCostSummaryContent.css';
import {
  Discounts,
  Shipping,
  Subtotal,
  Tax,
  Total,
  GiftWrapping,
  PrintedCard,
  GiftCards,
} from '@/order/components/OrderCostSummaryContent/Blocks';

export const OrderCostSummaryContent: FunctionComponent<
  OrderCostSummaryContentProps
> = ({
  translations,
  loading,
  storeConfig,
  order,
  withHeader = true,
}: OrderCostSummaryContentProps) => {
  if (loading || !order) return <OrderSummarySkeleton />;

  const totalGiftCardValue = order?.totalGiftCard?.value ?? 0;
  const totalGiftCardCurrency = order.totalGiftCard?.currency ?? '';
  const subtotalInclTax = order.subtotalInclTax?.value ?? 0;
  const subtotalExclTax = order.subtotalExclTax?.value ?? 0;
  const totalShipping = order.totalShipping?.value ?? 0;
  const renderTaxAccordion =
    !!order?.taxes?.length && storeConfig?.shoppingOrdersDisplayFullSummary!;
  const totalAccordionTaxValue = renderTaxAccordion
    ? order?.taxes?.reduce((value, acc) => {
        return +acc?.amount?.value + value;
      }, 0)
    : 0;

  return (
    <Card
      variant="secondary"
      className={classes(['order-cost-summary-content'])}
    >
      {withHeader ? <Header title={translations.headerText} /> : null}

      <div className="order-cost-summary-content__wrapper">
        <Subtotal
          translations={translations}
          order={order}
          subtotalInclTax={subtotalInclTax}
          subtotalExclTax={subtotalExclTax}
          shoppingOrdersDisplaySubtotal={
            storeConfig?.shoppingOrdersDisplaySubtotal!
          }
        />
        <Shipping
          translations={translations}
          order={order}
          totalShipping={totalShipping}
          shoppingOrdersDisplayShipping={
            storeConfig?.shoppingOrdersDisplayShipping!
          }
        />
        <PrintedCard
          order={order}
          translations={translations}
          salesPrintedCard={storeConfig?.salesPrintedCard}
        />
        <GiftWrapping
          salesGiftWrapping={storeConfig?.salesGiftWrapping}
          giftWrappingPrice={order.totalGiftOptions.giftWrappingForItems}
          giftWrappingPriceInclTax={
            order.totalGiftOptions.giftWrappingForItemsInclTax
          }
          giftWrappingTitle={translations.itemGiftWrappingTitle}
          giftWrappingExclTaxText={translations.itemGiftWrappingExclTax}
          giftWrappingInclTaxText={translations.itemGiftWrappingInclTax}
        />
        <GiftWrapping
          salesGiftWrapping={storeConfig?.salesGiftWrapping}
          giftWrappingPrice={order.totalGiftOptions.giftWrappingForOrder}
          giftWrappingPriceInclTax={
            order.totalGiftOptions.giftWrappingForOrderInclTax
          }
          giftWrappingTitle={translations.orderGiftWrappingTitle}
          giftWrappingExclTaxText={translations.orderGiftWrappingExclTax}
          giftWrappingInclTaxText={translations.orderGiftWrappingInclTax}
        />
        <Discounts translations={translations} order={order} />
        <GiftCards
          order={order}
          totalGiftCardValue={totalGiftCardValue}
          totalGiftCardCurrency={totalGiftCardCurrency}
        />
        <Tax
          order={order}
          translations={translations}
          renderTaxAccordion={renderTaxAccordion}
          totalAccordionTaxValue={totalAccordionTaxValue}
        />
        <Total
          translations={translations}
          shoppingOrdersDisplaySubtotal={
            storeConfig?.shoppingOrdersDisplaySubtotal!
          }
          order={order}
        />
      </div>
    </Card>
  );
};
