import { OrderDataModel } from '@/order/data/models';
import { TaxTypes } from '@/order/types';
import { Price } from '@adobe-commerce/elsie/components';

type translationsTypes = Record<string, string>;

export const Shipping = ({
  translations,
  shoppingOrdersDisplayShipping,
  order,
  totalShipping,
}: {
  totalShipping: number;
  shoppingOrdersDisplayShipping: TaxTypes;
  order: OrderDataModel;
  translations: translationsTypes;
}) => {
  if (order?.isVirtual) return null;

  return (
    <div className="order-cost-summary-content__description order-cost-summary-content__description--shipping">
      <div className="order-cost-summary-content__description--header">
        <span>{translations.shipping}</span>
        {order?.totalShipping?.value ? (
          <Price
            weight="normal"
            currency={order?.totalShipping?.currency}
            amount={totalShipping}
          />
        ) : (
          <span>{translations.freeShipping}</span>
        )}
      </div>
      <div className="order-cost-summary-content__description--subheader">
        {shoppingOrdersDisplayShipping.taxIncluded &&
        shoppingOrdersDisplayShipping.taxExcluded ? (
          <>
            <Price
              weight="normal"
              currency={order?.totalShipping?.currency}
              amount={order?.totalShipping?.value}
              size="small"
            />
            <span> {translations.excl}</span>
          </>
        ) : null}
        {shoppingOrdersDisplayShipping.taxIncluded &&
        !shoppingOrdersDisplayShipping.taxExcluded ? (
          <span>{translations.incl}</span>
        ) : null}
      </div>
    </div>
  );
};
