import { classes, Container } from '@adobe-commerce/elsie/lib';
import { useGetStoreConfig, useOrderCostSummary } from '@/order/hooks';
import { OrderCostSummaryProps } from '@/order/types';
import { OrderCostSummaryContent } from '@/order/components';
import { useText } from '@adobe-commerce/elsie/i18n';

export const OrderCostSummary: Container<OrderCostSummaryProps> = ({
  withHeader,
  orderData,
  children,
  className,
  ...props
}) => {
  const config = useGetStoreConfig();
  const { loading, storeConfig, order } = useOrderCostSummary({
    orderData,
    config,
  });

  const translations = useText({
    subtotal: 'Order.OrderCostSummary.subtotal.title',
    shipping: 'Order.OrderCostSummary.shipping.title',
    freeShipping: 'Order.OrderCostSummary.shipping.freeShipping',
    tax: 'Order.OrderCostSummary.tax.title',
    incl: 'Order.OrderCostSummary.tax.incl',
    excl: 'Order.OrderCostSummary.tax.excl',
    discount: 'Order.OrderCostSummary.discount.title',
    discountSubtitle: 'Order.OrderCostSummary.discount.subtitle',
    total: 'Order.OrderCostSummary.total.title',
    totalFree: 'Order.OrderCostSummary.totalFree',
    accordionTitle: 'Order.OrderCostSummary.tax.accordionTitle',
    accordionTotalTax: 'Order.OrderCostSummary.tax.accordionTotalTax',
    totalExcludingTaxes: 'Order.OrderCostSummary.tax.totalExcludingTaxes',
    headerText: 'Order.OrderCostSummary.headerText',

    printedCardTitle: 'Order.OrderCostSummary.giftOptionsTax.printedCard.title',
    printedCardTitleInclTax:
      'Order.OrderCostSummary.giftOptionsTax.printedCard.inclTax',
    printedCardTitleExclTax:
      'Order.OrderCostSummary.giftOptionsTax.printedCard.exclTax',

    itemGiftWrappingTitle:
      'Order.OrderCostSummary.giftOptionsTax.itemGiftWrapping.title',
    itemGiftWrappingInclTax:
      'Order.OrderCostSummary.giftOptionsTax.itemGiftWrapping.inclTax',
    itemGiftWrappingExclTax:
      'Order.OrderCostSummary.giftOptionsTax.itemGiftWrapping.exclTax',

    orderGiftWrappingTitle:
      'Order.OrderCostSummary.giftOptionsTax.orderGiftWrapping.title',
    orderGiftWrappingInclTax:
      'Order.OrderCostSummary.giftOptionsTax.orderGiftWrapping.inclTax',
    orderGiftWrappingExclTax:
      'Order.OrderCostSummary.giftOptionsTax.orderGiftWrapping.exclTax',
  });

  return (
    <div {...props} className={classes(['order-cost-summary', className])}>
      <OrderCostSummaryContent
        order={order}
        withHeader={withHeader}
        loading={loading}
        storeConfig={storeConfig}
        translations={translations}
      />
    </div>
  );
};
