import { OrderDataModel } from '@/order/data/models';
import { TaxTypes } from '@/order/types';
import { Price } from '@adobe-commerce/elsie/components';

type translationsTypes = Record<string, string>;

export const Subtotal = ({
  translations,
  order,
  subtotalInclTax,
  subtotalExclTax,
  shoppingOrdersDisplaySubtotal,
}: {
  translations: translationsTypes;
  order?: OrderDataModel;
  subtotalInclTax: number;
  subtotalExclTax: number;
  shoppingOrdersDisplaySubtotal: TaxTypes;
}) => {
  const isTaxIncluded = shoppingOrdersDisplaySubtotal.taxIncluded;
  const isTaxExcluded = shoppingOrdersDisplaySubtotal.taxExcluded;

  const taxIncludedView =
    isTaxIncluded && !isTaxExcluded ? (
      <>
        <div className="order-cost-summary-content__description--header">
          <span>{translations.subtotal}</span>
          <Price
            className="order-cost-summary-content__description--normal-price"
            weight="normal"
            currency={order?.subtotalInclTax?.currency}
            amount={subtotalInclTax}
          />
        </div>
        <div className="order-cost-summary-content__description--subheader">
          <span>{translations.incl}</span>
        </div>
      </>
    ) : null;

  const taxExcludedView =
    isTaxExcluded && !isTaxIncluded ? (
      <div className="order-cost-summary-content__description--header">
        <span>{translations.subtotal}</span>
        <Price
          className="order-cost-summary-content__description--normal-price"
          weight="normal"
          currency={order?.subtotalExclTax?.currency}
          amount={subtotalExclTax}
        />
      </div>
    ) : null;

  const taxIncludedAndExcludedView =
    isTaxExcluded && isTaxIncluded ? (
      <>
        <div className="order-cost-summary-content__description--header">
          <span>{translations.subtotal}</span>
          <Price
            className="order-cost-summary-content__description--normal-price"
            weight="normal"
            currency={order?.subtotalInclTax?.currency}
            amount={subtotalInclTax}
          />
        </div>
        <div className="order-cost-summary-content__description--subheader">
          <Price
            currency={order?.subtotalExclTax?.currency}
            amount={subtotalExclTax}
            size="small"
            weight={'bold'}
          />{' '}
          <span>{translations.excl}</span>
        </div>
      </>
    ) : null;

  return (
    <div className="order-cost-summary-content__description order-cost-summary-content__description--subtotal">
      {taxIncludedView}
      {taxExcludedView}
      {taxIncludedAndExcludedView}
    </div>
  );
};
