import { OrderDataModel } from '@/order/data/models';
import { Icon, Price } from '@adobe-commerce/elsie/components';
import { Text } from '@adobe-commerce/elsie/i18n';
import { GiftCard } from '@adobe-commerce/elsie/icons';

export const GiftCards = ({
  totalGiftCardValue,
  totalGiftCardCurrency,
  order,
}: {
  order: OrderDataModel;
  totalGiftCardValue: number;
  totalGiftCardCurrency: string;
}) => {
  const appliedGiftCards = order?.appliedGiftCards;

  if (!totalGiftCardValue) return null;

  return (
    <div className="order-cost-summary-content__description order-cost-summary-content__description--discount">
      <div className="order-cost-summary-content__description--header">
        <span>
          <Text
            id="Order.OrderCostSummary.appliedGiftCards.label"
            plural={appliedGiftCards?.length}
            fields={{
              count: appliedGiftCards?.length,
            }}
          />
        </span>
        <span>
          <Price
            weight="normal"
            sale
            currency={totalGiftCardCurrency}
            amount={-totalGiftCardValue}
          />
        </span>
      </div>

      {appliedGiftCards.map((el) => {
        return (
          <div
            className="order-cost-summary-content__description--subheader"
            key={el.code}
          >
            <span>
              <Icon
                source={GiftCard}
                size="16"
                className="order-cost-summary-content__description--giftcard-icon"
              />
              <span>{el.code}</span>
            </span>
          </div>
        );
      })}
    </div>
  );
};
