import { OrderDataModel } from '@/order/data/models';
import { setTaxStatus } from '@/order/lib/setTaxStatus';
import { StoreConfigProps, UseOrderCostSummaryProps } from '@/order/types';
import { events } from '@adobe-commerce/event-bus';
import { useState, useEffect } from 'preact/hooks';

export const useOrderCostSummary = ({
  orderData,
  config,
}: UseOrderCostSummaryProps) => {
  const [loading, setLoading] = useState(true);
  const [order, setOrder] = useState(orderData);
  const [storeConfig, setStoreConfig] = useState<StoreConfigProps | null>(null);

  useEffect(() => {
    if (config) {
      const {
        shoppingOrderDisplayPrice,
        shoppingOrdersDisplayShipping,
        shoppingOrdersDisplaySubtotal,
        ...props
      } = config;

      setStoreConfig((prev) => ({
        ...prev,
        ...props,
        shoppingOrderDisplayPrice: setTaxStatus(shoppingOrderDisplayPrice),
        shoppingOrdersDisplayShipping: setTaxStatus(
          shoppingOrdersDisplayShipping
        ),
        shoppingOrdersDisplaySubtotal: setTaxStatus(
          shoppingOrdersDisplaySubtotal
        ),
        salesPrintedCard: setTaxStatus(config.salesPrintedCard),
        salesGiftWrapping: setTaxStatus(config.salesGiftWrapping),
      }));
      setLoading(false);
    }
  }, [config]);

  useEffect(() => {
    const event = events.on(
      'order/data',
      (order: OrderDataModel) => {
        setOrder(order);
      },
      { eager: true }
    );

    return () => {
      event?.off();
    };
  }, []);

  return { loading, storeConfig, order };
};
