import { OrderDataModel } from '@/order/data/models';
import { Icon, Price } from '@adobe-commerce/elsie/components';
import { Coupon } from '@adobe-commerce/elsie/icons';

type translationsTypes = Record<string, string>;

export const Discounts = ({
  translations,
  order,
}: {
  order: OrderDataModel;
  translations: translationsTypes;
}) => {
  const appliedCoupons = order?.coupons;
  const discounts = order?.discounts;
  const discountValue =
    discounts?.reduce((acc, item) => {
      return acc + (item.amount.value ?? 0);
    }, 0) || 0;

  if (!order?.discounts?.length && discountValue === 0) return null;

  return (
    <div className="order-cost-summary-content__description order-cost-summary-content__description--discount">
      <div className="order-cost-summary-content__description--header">
        <span>{translations.discount}</span>
        <span>
          <Price
            weight="normal"
            sale
            currency={discounts[0]?.amount.currency}
            amount={-discountValue}
          />
        </span>
      </div>

      {appliedCoupons.map((el) => {
        return (
          <div
            className="order-cost-summary-content__description--subheader"
            key={el.code}
          >
            <span>
              <Icon
                source={Coupon}
                size="16"
                className="order-cost-summary-content__description--coupon-icon"
              />
              <span>{el.code}</span>
            </span>
          </div>
        );
      })}
    </div>
  );
};
